<template>
    <div>
        <div v-show="show">
            <div class="alert-model">
            </div>
            <div class="msg-alert">
                <div v-show="ModelOptions.img" class="msg-img">
                    <img :src="ModelOptions.imgsrc" alt="">
                </div>
                <div v-show="ModelOptions.type=='1'" class="msg-content">
                    <p class="msg-content-title">{{ ModelOptions.title }}</p>
                    <p class="msg-content-text">{{ModelOptions.message}}</p>
                </div>
                <div v-show="ModelOptions.type=='2'" class="msg-content">
                    <p class="msg-content-title">{{ ModelOptions.title }}</p>
                    <textarea type="text" v-model="ModelOptions.inputvalue" :readonly="ModelOptions.readonly"></textarea>
                </div>
                <div v-show="ModelOptions.notice && ModelOptions.notice!='' && ModelOptions.notice!= 'undefined'" class="msg-notice">
                    <p>{{ModelOptions.notice}}</p>
                </div>
                <div class="msg-button">
                    <button @click="modelCancel" :class="!ModelOptions.btnOk?'msg-button-s':'msg-button-two'">{{ ModelOptions.btnText ? ModelOptions.btnText :'取消'}}</button>
                    <button v-show="ModelOptions.btnOk" @click="modelok" class="msg-button-two msg-button-two-be">{{ ModelOptions.btnOk ? ModelOptions.btnOk :'确定'}}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ["ModelOptions"],
        data() {
            return {
                show:false,
            }
        },
        methods:{
            showModel(){
                this.show=true;
            },
            closeModel(){
                this.show = false;
            },
            modelCancel: function () {
                let that=this;
                this.show = false;
                if(typeof (this.ModelOptions.btnCancelFunction)==='function'){
                    this.ModelOptions.btnCancelFunction(that.ModelOptions)
                }
            },
            modelok(){
                let that=this;
                this.show = false;
                if(typeof (this.ModelOptions.btnOkFunction)==='function'){
                    this.ModelOptions.btnOkFunction(that.ModelOptions)
                }
            }
        }
    }
</script>

<style lang="less" scoped>
.alert-model{
    position: absolute;
    width:100%;
    height:100%;
    top:0;
    left:0;
    background:#000;
    opacity:0.7;
    z-index:2001;
    padding:5rem;
    
}
.msg-alert{
    position: absolute;
    background: #fff;
    border-radius: 13px;
    width:80%;
    top:35%;
    z-index:2002;
    min-height:5rem;
    left:50%;
    transform: translate3d(-50%, -50%, 0);
    .msg-img{
        text-align: center;
        padding-top: 25px;
        img{
            width:40px;
        }
    }
    .msg-notice{
        padding:10px 25px;
        text-align: center;
        color:#fc5d9f;
        font-size:14px;
    }
    .msg-content{
        padding: 20px 20px 0px;
        text-align: center;
        .msg-content-title{
            font-size:16px;
            color:#1f1f2b;
            margin-bottom: 10px;
            font-weight:600;
        }
        .msg-content-text{
            color:#2e2f47;
            font-size:14px;
            margin-bottom: 20px;
        }
        textarea{
            background:#eeeeee;
            border-radius:13px;
            width:100%;
            min-height:60px;
            border:none;
            resize: none;
            padding:10px;
            color:#2e2f47;
            font-size:12px;
        }
    }
    .msg-button{
        height: 50px;
        background: linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
        border-radius: 0 0 13px 13px;
        button.msg-button-s {
            width: 100%;
            line-height: 48px;
            font-size: 16px;
            color: #fff;
        }
        .msg-button-two{
            width: 50%;
            line-height: 48px;
            font-size: 16px;
            color: #fff;
            float: left;
        }
        .msg-button-two-be{
            position: relative;
        }
        .msg-button-two-be::before{
            content: "";
            display:inline-block;
            width:1px;
            height:100%;
            background: #fff;
            position: absolute;
            left:0;
            top:0;
        }
    }
    
}
</style>


