import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/home';
import Home1 from '@/components/home/home1';
import Guide from '@/components/guide/guide';
import Created from "@/components/created/created";
import CreSuccess from '@/components/created/cresuccess';
import Leadin from '@/components/leadin/leadin';
import Backups from '@/components/backups/backups';
import Verification from '@/components/backups/verification';
import Addassets from '@/components/addassets/addassets';
import Search from '@/components/addassets/search';
import Account from "@/components/account/account";
import Receivables from '@/components/receivables/Receivables';
import Details from '@/components/home/assetdetails';
import Detailinfo from '@/components/home/detailinfo';
import Paydetail from '@/components/account/paydetail';
import Set from '@/components/set/set';
import Addresslist from '@/components/set/addresslist';
import Addressadd from '@/components/set/addressadd';
import Pursemanage from '@/components/set/pursemanage';
import WalletInfo from '@/components/set/wallet_info';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',//首页
      name: 'Home',
      component: Home
    },
    {
      path: '/set',//设置
      name: 'Set',
      component: Set
    },
    {
      path: '/pursemanage',//钱包管理
      name: 'Pursemanage',
      component: Pursemanage
    },
    {
      path: '/walletinfo',//钱包信息
      name: 'WalletInfo',
      component: WalletInfo
    },
    {
      path: '/addresslist',//地址列表
      name: 'Addresslist',
      component: Addresslist
    },
    {
      path: '/addressadd',//地址添加
      name: 'Addressadd',
      component: Addressadd
    },
    {
      path: '/details',//资产详情
      name: 'Details',
      component: Details
    },
    {
      path: '/detailinfo',//资产详情
      name:'Detailinfo',
      component:Detailinfo
    },
    {
      path: '/guide',//引导页
      name: 'Guide',
      component: Guide
    },
    {
      path: '/created',//创建钱包
      name: 'Created',
      component: Created
    },
    {
      path: '/cresuccess',//创建成功
      name: 'CreSuccess',
      component: CreSuccess
    },
    {
      path: '/leadin',//导入钱包
      name: 'Leadin',
      component: Leadin
    },
    {
      path: '/backups',//备份助记词
      name: 'Backups',
      component: Backups
    },
    {
      path: '/verification',//验证助记词
      name: 'Verification',
      component: Verification
    },
    {
      path: '/addassets',//添加资产
      name: 'Addassets',
      component: Addassets
    },
    {
      path: '/search',//添加资产搜索
      name: 'Search',
      component: Search
    },
    {
      path: '/account',//转账
      name: 'Account',
      component: Account
    },
    {
      path: '/paydetail',//支付详情
      name: 'Paydetail',
      component: Paydetail
    },
    {
      path: '/receivables',//收款码
      name: 'Receivables',
      component: Receivables
    }
  ]
})



