<template>
    <div class="address_list main-content">
        <mt-header class="created-header" fixed title="地址管理">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
            <mt-button slot="right" v-show="vShow" @click="edit" class="bianji">编辑</mt-button>
            <mt-button slot="right" v-show="!vShow" @click ="delete_address" class="delete_address">删除</mt-button>
        </mt-header>
        <div class="account-form">
            <div style='min-height:80%;'>
                <div v-for="(item,index) in address_list" :key="index" class="account-form-name">
                    <!-- <input type="radio" name="address_list"  :value="item.id"> -->
                    <span :class="{on:index == s_index,radio_input:true}" @click="clickradio(index,item)"></span>
                    <p>地址{{ index+1 }}</p>
                    <input type="text"  placeholder="请输入收款地址" :value="item.address" readonly>
                </div>
            </div>
            <div style="min-height:15%" v-show="vShow">
                <mt-button @click.native="address_add" class="add_address">
                    添加新地址
                </mt-button>
            </div>
            
        </div>
        <Alert :ModelOptions="confirmOptions" ref="alertModel"></Alert>
    </div>
</template>

<script>
import Alert from '../com/alert.vue';

    export default {
        components:{
            Alert
        },
        data(){
            return{
                vShow:true,
                confirmOptions:{},
                s_index:-1,
                address_id:"",
                address_list:[
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    }
                ]
            }
        },
        methods:{
            clickradio(index,item){
                console.log(index,item)
                this.s_index = index;
                this.address_id=item.id
                
            },
            edit(){
                if(this.address_id){
                    this.vShow = false;
                }
            },
            delete_address(){
                console.log("delete-----")
                let that =this;
                this.$refs.alertModel.showModel();
                this.confirmOptions= {
                    img:false,//弹框显示图片 需要传imgsrc
                    imgsrc:'/static/img/shibai.png',//图片路径
                    type: "1",// 1:普通提示框 2:textarea提示框，需要传入 inputvalue,readonly
                    inputvalue:"qwr124f1wt24tef34t235g23yj4fqwf567f23fgtrbqewftyn43btn",//textarea的值
                    readonly:false,// 是否只读
                    title: "确认要删除此地址吗？",//标题
                    message: "",//提示信息
                    btnText: "取消",//取消按钮文字
                    btnOk:"确定",// 确认按钮文字
                    notice:"",//特殊警告信息   如：警告：私钥未加密，泄漏风险极大，请保存在安全位置！,
                    confirm:true,// 开启 确定 取消两个按钮
                    btnCancelFunction: function (val) {//回调函数
                        // console.log(that.confirmOptions.inputvalue)
                        // alert(val)
                    },
                    btnOkFunction: function (val) {//回调函数
                        console.log(that.confirmOptions)
                        // alert(val)
                    },
                }
            },
            address_add(){
                console.log('--------')
                this.$router.push('/addressadd')
            }
        }
    }
</script>

<style lang="less">
.address_list{
    .created-header{
        .mint-button{
            .mint-button-text{
                padding-right:20px;
            }
        }
        .delete_address{
            color:#fc5d9f;
        }
    }
    .account-form{
        width:100%;
        height:100%;
        padding: 3.4rem 3rem 3rem;
        min-height:100%;
        position: relative;

        .account-form-name{
            margin-bottom: 3rem;
            position:relative;
            .radio_input{
                position:absolute;
                top:0;
                right:0;
                display: inline-block;
                width:25px;
                height:25px;
                border:1px solid #e0e0e0;
                border-radius: 20px;
            }
            .on::after{
                content: "";
                display: inline-block;
                width:12px;
                height:12px;
                border-radius: 50%;
                background: #e0e0e0;
                position: absolute;
                left:50%;
                top:50%;
                transform: translate3d(-50%,-50%,0);
            }
            input::-webkit-input-placeholder { /* WebKit browsers */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 

            input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            .text_error{
                height:35px;
                line-height: 30px;
                color:#f00;
                font-size:12px;
            }
            p{
                font-size:12px;
                color: #66688f;
            }
            input{
                width:100%;
                border:none;
                border-bottom:1px solid #2e2f47;
                background: none;
                padding:22px 0;
                font-size: 16px;
                line-height: 2rem;
                color:#fff;
            }
            .account-range{
                margin-top:2rem;
                .mt-range-runway{
                    border-radius: 10px;
                }
                .mt-range-progress{
                    border-radius: 10px 0 0 10px;
                    background-color:#bf5fc1;
                }
                .mt-range-thumb{
                    // width:21px;
                    // height:21px;
                    // top:4px;
                }
                
            }
            .icon-shows{
                color: #fff;
                position: absolute;
                right: 0;
                top: 50%;
                font-size:20px;
                transform: translate3d(0,-50%,0);
            }
        }
        .account-button{
            // position: absolute;
            text-align: center;
            width:100%;
            margin-top: 10rem;;
            // bottom:80px;
            p{
                font-size:12px;
                color:#66688f;
                letter-spacing:0.6px;
                text-align:center;
                line-height:14px;
                margin-bottom: 17px;
            }
            .cj-button{
                border:none;
                background: none;
                box-shadow: none;
                width: 100%;
            }
            .cj-button:hover{
                background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
            .selected{
                background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
        }
    }
    .add_address{
        background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
        border-radius:100px;
        height:50px;
        border:none;
        margin-bottom: 20px;
        color: #fff;
        width:100%;
    }
}
</style>


