<template>
    <div class="paydetail main-content">
         <mt-header class="created-header" fixed title="支付详情">
            <mt-button @click="historys" slot="left" icon="back"></mt-button>
        </mt-header>
        <div class="account-form" >
            <div v-show="xinxi">
                <div class="account-form-name">
                    <p>订单信息</p>
                    <input type="text"  placeholder="" readonly>
                </div>
                <div class="account-form-name">
                    <p>转入地址</p>
                    <input type="text" placeholder="" readonly>
                </div>
                <div class="account-form-name">
                    <p>付款钱包</p>
                    <input type="text" placeholder="" readonly>
                </div>
                <div class="account-form-name">
                    <p>矿工费</p>
                    <input type="text" placeholder="" readonly >
                </div>
                <div class="account-form-name">
                    <p>金额</p>
                    <input type="text" placeholder="" readonly>
                </div>
            </div>
            <div v-show="!xinxi">
                <div class="account-form-name">
                    <p>密码</p>
                    <div style="position:relative;">
                        <input :type="inputtype" placeholder="请输入密码" style="letter-spacing:5px;padding-right:30px;">
                        <i :class="head_show" @click="showpassword(show_pw)"></i>
                    </div>
                </div>
            </div>
            
            
            <div class="account-button">
                <mt-button @click.native="next" class="cj-button selected">
                    下一步
                </mt-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                xinxi:true,
                inputtype:"password",
                head_show:"icon-shows fa fa-yincang",
                show_pw:false
            }
        },
        methods:{
            next(){
                console.log("0000")
                this.xinxi = false;
            },
            historys(){
                console.log(this.xinxi)
                if(this.xinxi == false){
                    this.xinxi = true;
                }else{
                    this.$router.history.go(-1)
                }
            },
            showpassword(val){
                console.log(val)
                if(val){
                    this.show_pw = !this.show_pw;
                    this.head_show = "icon-shows fa fa-yincang";
                    this.inputtype="password"
                }else{
                    this.show_pw = !this.show_pw;
                    this.head_show = "icon-shows fa fa-xianshi";
                    this.inputtype = "text"
                }
            }
        }
    }
</script>

<style lang="less">
.paydetail{


    .account-form{
        width:100%;
        padding: 3.4rem 3rem 3rem;
        min-height:100%;
        position: relative;

        .account-form-name{
            margin-bottom: 3rem;
            input::-webkit-input-placeholder { /* WebKit browsers */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 

            input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            .text_error{
                height:35px;
                line-height: 30px;
                color:#f00;
                font-size:12px;
            }
            p{
                font-size:12px;
                color: #66688f;
            }
            input{
                width:100%;
                border:none;
                border-bottom:1px solid #2e2f47;
                background: none;
                padding:22px 0;
                font-size: 16px;
                line-height: 2rem;
                color:#fff;
            }
            .account-range{
                margin-top:2rem;
                .mt-range-runway{
                    border-radius: 10px;
                }
                .mt-range-progress{
                    border-radius: 10px 0 0 10px;
                    background-color:#bf5fc1;
                }
                .mt-range-thumb{
                    // width:21px;
                    // height:21px;
                    // top:4px;
                }
                
            }
            .icon-shows{
                color: #fff;
                position: absolute;
                right: 0;
                top: 50%;
                font-size:20px;
                transform: translate3d(0,-50%,0);
            }
        }
        .account-button{
            // position: absolute;
            text-align: center;
            width:100%;
            margin-top: 10rem;;
            // bottom:80px;
            p{
                font-size:12px;
                color:#66688f;
                letter-spacing:0.6px;
                text-align:center;
                line-height:14px;
                margin-bottom: 17px;
            }
            .cj-button{
                border:none;
                background: none;
                box-shadow: none;
                width: 100%;
            }
            .cj-button:hover{
                background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
            .selected{
                background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
        }
    }
}
</style>


