<template>
    <div class="purse-manage main-content">
        <mt-header class="created-header" fixed title="钱包管理">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
        </mt-header>
        <div class="account-form">
            <div style='min-height:80%;'>
                <div v-for="(item,index) in wallet_list" :key="index" @click="walletinfo(item)" class="account-form-name">
                    <p>地址{{ index+1 }}</p>
                    <input type="text"  placeholder="请输入收款地址" :value="item.address" readonly>
                </div>
            </div>
            <div style="min-height:15%;margin-bottom:50px;">
                <mt-button @click.native="newpurse(1)" :class="selected_newpurse?'cj-button selected':'cj-button'">
                    创建钱包
                </mt-button>
                <mt-button @click.native="newpurse(2)"  :class="selected_import?'cj-button selected':'cj-button'">
                    导入钱包
                </mt-button>
            </div>
        </div>
    </div>
</template>

<script>
import Alert from '../com/alert.vue';

    export default {
        components:{
            Alert
        },
        data(){
            return{
                selected_newpurse:true,
                selected_import:false,
                address_id:"",
                wallet_list:[
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    },
                    {
                        address:"fh32th4re98twu4hrgewh834",
                        id:"2345346"
                    }
                ]
            }
        },
        methods:{
            walletinfo(item){
                console.log(item);
                this.$router.push('/walletinfo');
            },
            newpurse(val){
                if(val == "1"){
                    this.selected_newpurse = true;
                    this.selected_import=false;
                    this.$router.push({
                        path:'/created',
                        query:{
                            type:'1'
                        }
                    })
                }else{
                    this.selected_newpurse = false;
                    this.selected_import=true;
                    this.$router.push('/leadin')
                }
            }
        }
    }
</script>

<style lang="less">
.purse-manage{
    .account-form{
        width:100%;
        height:100%;
        padding: 3.4rem 3rem 3rem;
        min-height:100%;
        position: relative;

        .account-form-name{
            margin-bottom: 3rem;
            position:relative;
            .radio_input{
                position:absolute;
                top:0;
                right:0;
                display: inline-block;
                width:25px;
                height:25px;
                border:1px solid #e0e0e0;
                border-radius: 20px;
            }
            .on::after{
                content: "";
                display: inline-block;
                width:12px;
                height:12px;
                border-radius: 50%;
                background: #e0e0e0;
                position:relative;
                left:50%;
                top:50%;
                transform: translate3d(-50%,-50%,0);
            }
            input::-webkit-input-placeholder { /* WebKit browsers */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 

            input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
                font-size:16px;
                letter-spacing:1px;
                color:#fff;
            } 
            .text_error{
                height:35px;
                line-height: 30px;
                color:#f00;
                font-size:12px;
            }
            p{
                font-size:12px;
                color: #66688f;
            }
            input{
                width:100%;
                border:none;
                border-bottom:1px solid #2e2f47;
                background: none;
                padding:22px 0;
                font-size: 16px;
                line-height: 2rem;
                color:#fff;
            }
            .account-range{
                margin-top:2rem;
                .mt-range-runway{
                    border-radius: 10px;
                }
                .mt-range-progress{
                    border-radius: 10px 0 0 10px;
                    background-color:#bf5fc1;
                }
                .mt-range-thumb{
                    // width:21px;
                    // height:21px;
                    // top:4px;
                }
                
            }
            .icon-shows{
                color: #fff;
                position: absolute;
                right: 0;
                top: 50%;
                font-size:20px;
                transform: translate3d(0,-50%,0);
            }
        }
        .account-button{
            // position: absolute;
            text-align: center;
            width:100%;
            margin-top: 10rem;;
            // bottom:80px;
            p{
                font-size:12px;
                color:#66688f;
                letter-spacing:0.6px;
                text-align:center;
                line-height:14px;
                margin-bottom: 17px;
            }
            .cj-button{
                border:none;
                background: none;
                box-shadow: none;
                width: 100%;
            }
            .cj-button:hover{
                background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
            .selected{
                background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
        }
        .cj-button{
            border:none;
            background: none;
            box-shadow: none;
            width: 100%;
        }
        .cj-button:hover{
            background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
            border-radius:100px;
            width:100%;
            height:50px;
            border:none;
            margin-bottom: 10px;
        }
        .selected{
            background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
            border-radius:100px;
            width:100%;
            height:50px;
            border:none;
            margin-bottom: 10px;
            color: #fff;
        }            
    }
    
}
</style>


