// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import { Button, Cell,Actionsheet,Swipe, SwipeItem,Header, Radio,
          Navbar,Switch, TabItem,TabContainer, TabContainerItem,Range,InfiniteScroll
    } from 'mint-ui';
import 'mint-ui/lib/style.css';
import './assets/font/iconfont.css';
import './assets/css/my-mint.less';
Vue.config.productionTip = false;

Vue.component(Radio.name, Radio);
Vue.use(InfiniteScroll);
Vue.component(Range.name, Range);
Vue.component(Switch.name, Switch);
Vue.component(TabContainer.name, TabContainer);
Vue.component(TabContainerItem.name, TabContainerItem);
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
Vue.component(Header.name, Header);
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Button.name, Button);
Vue.component(Cell.name, Cell);
Vue.component(Actionsheet.name, Actionsheet);
let isfirst = localStorage.getItem('isfirstlogin'); // 是否第一次进入
console.log(isfirst)
