<template>
    <div class="wallet-info main-content">
        <mt-header class="created-header" fixed title="钱包1">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
        </mt-header>
        <div class="wallet-info-list">
            <div class="top-scroll">
                <div class="list_name">
                    钱包ID
                    <i class="">GFYUGVH%10px</i>
                </div>
                <div class="list_name">
                    修改密码
                    <i class="fa fa-you"></i>
                </div>
                <div class="list_name">
                    导出私钥
                    <i class="fa fa-you"></i>
                </div>
                <div class="list_name">
                    导出Keystore
                    <i class="fa fa-you"></i>
                </div>
            </div>
            <div class="bottom-button" style="padding:0 20px;">
                <mt-button @click.native="benfen(1)" :class="selected_newpurse?'cj-button selected':'cj-button'">
                    备份助记词
                </mt-button>
                <mt-button @click.native="benfen(2)"  :class="selected_import?'cj-button selected':'cj-button'">
                    删除钱包
                </mt-button>
            </div>
            
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                selected_newpurse:true,
                selected_import:false,
            }
        },
        methods:{
            benfen(val){
                console.log(val)
                if(val==1){
                        this.selected_newpurse = true;
                        this.selected_import = false;
                }else{
                        this.selected_newpurse = false;
                        this.selected_import = true;
                }
            }
        }
    }
</script>

<style lang="less">
.wallet-info{
    color:#fff;
    .wallet-info-list{
        height:100%;
    }
    .list_name{
        height:50px;
        line-height: 50px;
        font-size:14px;
        background: #2e2f47;
        margin-bottom:10px;
        padding-left:3rem;
        i{
            float:right;
            margin-right:10px;
            font-style:normal;
        }
    }
}
</style>


