<template>
    <div class="guide">
        <mt-swipe class="guide-swiper" @change="handleChange" :auto="2000" :continuous="false" :showIndicators="showIndicators" :speed="200" :prevent="false">
            <mt-swipe-item class="guide-swiper-list">
                <div class="guide-top-img">
                    <img src="/static/img/qidong2.png" alt="">
                </div>
                <div class="guide-text">
                    <h2>Three competing store</h2>
                    <p>
                        Three competing store owners rented adjoining shops in a mall. Observers waited for mayhem to ensue.
                    </p>
                </div>
            </mt-swipe-item>
            <mt-swipe-item class="guide-swiper-list">
                <div class="guide-top-img">
                    <img src="/static/img/qidong1.png" alt="">
                </div>
                <div class="guide-text">
                    <h2>Three competing store</h2>
                    <p>
                        Three competing store owners rented adjoining shops in a mall. Observers waited for mayhem to ensue.
                    </p>
                </div>
            </mt-swipe-item>
            <mt-swipe-item class="guide-swiper-list">
                <div class="guide-top-img">
                    <img src="/static/img/qidong3.png" alt="">
                </div>
                <div class="guide-text">
                    <h2>Three competing store</h2>
                    <p>
                        Three competing store owners rented adjoining shops in a mall. Observers waited for mayhem to ensue.
                    </p>
                </div>
            </mt-swipe-item>
            <mt-swipe-item class="guide-swiper-list tocreated" style="overflow-y: auto;">
                <div class="guide-top-img" style="height:auto;">
                    <img src="/static/img/qidong3.png" alt="">
                </div>
                <div class="guide-text">
                    <h2 style="font-size:36px;text-align:left;line-height:45px;padding-right:50px">Welcome to Coodex All in one</h2>
                    <p style="text-align:left;">
                        Three competing store owners rented adjoining .
                    </p>
                </div>
                <div class="guide-button">
                    <p style="font-size: 12px;color: #66688f;letter-spacing: 0.6px;text-align: center;line-height: 14px;margin-bottom: 17px;">阅读并同意《Codex服务条款》</p>
                    <mt-button @click.native="newpurse(1)" :class="selected_newpurse?'cj-button selected':'cj-button'">
                        创建钱包
                    </mt-button>
                    <mt-button @click.native="newpurse(2)"  :class="selected_import?'cj-button selected':'cj-button'">
                        导入钱包
                    </mt-button>
                </div>
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                selected_newpurse:true,
                selected_import:false,
                showIndicators:true
            }
        },
        methods:{
            handleChange(index){
                console.log(index)
                if(index == 3){
                    this.showIndicators = false;
                }else{
                    this.showIndicators = true;
                }
            },
            newpurse(val){
                if(val == "1"){
                    this.selected_newpurse = true;
                    this.selected_import=false;
                    this.$router.push({
                        path:'/created',
                        query:{
                            type:'1'
                        }
                    })
                }else{
                    this.selected_newpurse = false;
                    this.selected_import = true;
                    this.$router.push('/leadin')
                }
            }
        }
    }
</script>
<style lang="less">
    .guide{
        width:100%;
        height:100%;
        .guide-swiper{
            width: 100%;
            height:100%;
            .guide-swiper-list{
                width: 100%;
                height:100%;
                background: #191a2b;
                .guide-top-img{
                    width:100%;
                    height:30%;
                    margin-top:25%;
                    // background: #fff;
                    // opacity: 0.1;
                    margin-bottom: 20px;
                    padding:0 5rem;
                    img{
                        width:100%;
                    }
                }
                .guide-text{
                    padding:20px 24px 10px 24px;
                    color: #fff;
                    text-align: center;
                    h2{
                        font-family:Rubik-Medium;
                        font-size:24px;
                        color:#ffffff;
                        letter-spacing:0;
                        text-align:center;
                        line-height:24px;
                    }
                    p{
                        font-family:Rubik-Regular;
                        margin-top: 16px;
                        font-size:16px;
                        color:#9c9eb9;
                        letter-spacing:0;
                        text-align:center;
                        line-height:25px;
                    }
                }
                
                .guide-button{
                    position: absolute;
                    text-align: center;
                    width:100%;
                    bottom:80px;
                    margin-top:5rem;
                    .cj-button{
                        border:none;
                        background: none;
                        box-shadow: none;
                        width: 100%;
                    }
                    .cj-button:hover{
                        background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                        border-radius:100px;
                        width:80%;
                        height:50px;
                        border:none;
                        margin-bottom: 10px;
                        color: #fff;
                    }
                    .selected{
                        background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                        border-radius:100px;
                        width:80%;
                        height:50px;
                        border:none;
                        margin-bottom: 10px;
                        color: #fff;
                    }
                }
                @media screen and (max-width:320px) {
                    .guide-button{
                        position: static;
                        text-align: center;
                        width:100%;
                        // bottom:80px;
                        margin-top:5rem;
                        margin-bottom: 5rem;

                    }
                    
                }
            }
            .mint-swipe-indicators{
                margin-bottom: 30px;
                .mint-swipe-indicator{
                    background: #fff;
                    opacity:0.3;
                }
                .is-active{
                    background: #fff;
                    opacity: 1;
                }
            }
        }
    }
</style>



