<template>
    <div class="created main-content">
        <mt-header class="created-header" fixed title="创建钱包">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
        </mt-header>
        <div class="created-form">
            <div class="created-form-name">
                <p>钱包ID</p>
                <input type="text" v-model="walletid" @blur="change_walletid" placeholder="请输入6-20位字符，以字⺟母开头">
                <div class="text_error">
                    {{ idtext_error }}
                </div>
            </div>
            <div class="created-form-name">
                <p>密码</p>
                <input type="password" v-model="walletpassword" @blur="change_walletpass(1)" placeholder="请输入6-20位字符，需包含字⺟⼤小写" style="letter-spacing:5px;">
                <div class="text_error">
                    {{ pw_error }}
                </div>
            </div>
            <div class="created-form-name">
                <p>确认密码</p>
                <input type="password" v-model="walletpassword1" @blur="change_walletpass(2)" placeholder="请输入6-20位字符，需包含字⺟大小写" style="letter-spacing:5px;">
                <div class="text_error">
                    {{ pw2_error }}
                </div>
            </div>
            <div class="created-button">
                <p>阅读并同意《Codex服务条款》</p>
                <mt-button @click.native="newpurse(1)" :class="selected_newpurse?'cj-button selected':'cj-button'">
                    同意并创建
                </mt-button>
                <!-- <mt-button @click.native="newpurse(2)"  :class="selected_import?'cj-button selected':'cj-button'">
                    导入钱包
                </mt-button> -->
            </div>
        </div>
    </div>
</template>

<script>
import { MessageBox } from 'mint-ui';

    export default {
        data() {
            return {
                walletid:'',
                selected_newpurse:true,
                selected_import:false,
                idtext_error:"",
                walletpassword:'',
                walletpassword1:'',
                pw_error:'',
                pw2_error:'',
                idrule:/^[a-zA-Z]\w{5,19}$/,
                pwrule:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{6,20}$/,
                type:null
                
            }
        },
        created(){
            console.log(this.$route.query.type )
            this.type = this.$route.query.type 
        },
        methods:{
            change_walletid(e){
                // console.log(this.walletid)
                // let wallId = /^[a-zA-Z]\w{5,19}$/;
                // console.log(this.idrule.test(this.walletid))
                if(this.idrule.test(this.walletid)){
                    this.idtext_error = ""
                }else{
                    this.idtext_error = "钱包ID格式错误，请重新输入"
                }
                // /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/

            },
            change_walletpass(val){
                let wallPass = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{6,20}$/;
                if(val == 1){
                    if(this.pwrule.test(this.walletpassword)){
                        this.pw_error = ""
                    }else{
                        this.pw_error = "密码格式错误，请重新输入"
                    }
                }
                if(val ==2){
                    if(this.pwrule.test(this.walletpassword1)){
                        this.pw2_error = ""
                    }else{
                        this.pw2_error = "密码格式错误，请重新输入"
                    }
                }
                
                // /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/
            },
            newpurse(val){
                if(val == "1"){
                    this.selected_newpurse = true;
                    this.selected_import=false;
                    
                    if(!this.walletid){
                        this.idtext_error = "请输入钱包ID";
                        return ;
                    }
                    if(!this.walletpassword){
                        this.pw_error = "请输入密码";
                        return ;
                    }
                    if(!this.walletpassword1){
                        this.pw2_error = "请输入确认密码";
                        return ;
                    }
                    if(this.walletid && this.walletpassword && this.walletpassword1){
                        let _status = false;
                        if(this.idrule.test(this.walletid)){
                            this.idtext_error = ""
                        }else{
                            _status = true;
                            this.idtext_error = "钱包ID格式错误，请重新输入"
                        }
                        if(this.pwrule.test(this.walletpassword)){
                            this.pw_error = ""
                        }else{
                            _status = true;
                            this.pw_error = "密码格式错误，请重新输入"
                        }
                        if(this.pwrule.test(this.walletpassword1)){
                            this.pw2_error = ""
                        }else{
                            _status = true;
                            this.pw2_error = "密码格式错误，请重新输入"
                        }
                        if(_status){
                            return false;
                        }else{
                            if(this.walletpassword != this.walletpassword1){
                                MessageBox.alert("两次密码输入不一致", "提示")
                                .then(action => {
                                });
                                return ;
                            }else{
                                this.$router.push({
                                    path:"/cresuccess",
                                    query:{
                                        type:this.type
                                    }
                                })
                            }
                        }
                    }
                }else{
                    this.selected_newpurse = false;
                    this.selected_import=true;
                }
            }
        }
    }
</script>

<style lang="less">
    .created{
        padding-top:40px;
        height:100%;
        width:100%;
        overflow: auto;
        .created-header{
            background: none;
        }
        .created-form{
            width:100%;
            padding: 80px 30px 30px;
            min-height:100%;
            position: relative;

            .created-form-name{
                input::-webkit-input-placeholder { /* WebKit browsers */ 
                    font-size:1.2rem;
                    letter-spacing:1px;
                    color:#66688f;
                } 
                input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
                    font-size:1.2rem;
                    letter-spacing:1px;
                    color:#66688f;
                } 

                input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
                    font-size:1.2rem;
                    letter-spacing:1px;
                    color:#66688f;
                } 
                input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
                    font-size:1.2rem;
                    letter-spacing:1px;
                    color:#66688f;
                } 
                .text_error{
                    height:35px;
                    line-height: 30px;
                    color:#f00;
                    font-size:12px;
                }
                p{
                    font-size:12px;
                    color: #66688f;
                }
                input{
                    width:100%;
                    border:none;
                    border-bottom:1px solid #2e2f47;
                    background: none;
                    padding:22px 0;
                    font-size: 16px;
                    color:#fff;
                }
            }
            .created-button{
                // position: absolute;
                text-align: center;
                width:100%;
                margin-top: 15rem;;
                // bottom:80px;
                p{
                    font-size:12px;
                    color:#66688f;
                    letter-spacing:0.6px;
                    text-align:center;
                    line-height:14px;
                    margin-bottom: 17px;
                }
                .cj-button{
                    border:none;
                    background: none;
                    box-shadow: none;
                    width: 100%;
                }
                .cj-button:hover{
                    background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                    border-radius:100px;
                    height:50px;
                    border:none;
                    margin-bottom: 10px;
                    color: #fff;
                }
                .selected{
                    background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                    border-radius:100px;
                    height:50px;
                    border:none;
                    margin-bottom: 10px;
                    color: #fff;
                }
            }
        }
    }
</style>


