<template>
    <div class="cresuccess main-content">
        <div class="text-success">
            Wallet imported Successful
        </div>
        <div class="success-img">
            <div class="img-icon">
                <i class="fa fa-chenggong" style="font-size: 70px;text-align: center;line-height: 26rem;color:#4c4c57;"></i>
                <img class="img1" src="/static/img/success.png" alt="">
                <img class="img2" src="/static/img/success.png" alt="">
                <img class="img3" src="/static/img/success.png" alt="">
            </div>
            
        </div>
        <div class="cresuccess-button">
            
            <div v-if="$route.query.type=='1'">
                <p>钱包创建成功，请在使用前将助记词储存到安全地方做好钱包备份</p>
                <mt-button @click.native="newpurse_S(1)" :class="selected_newpurse?'cj-button selected':'cj-button'">
                    备份钱包
                </mt-button>
                <mt-button @click.native="newpurse_S(2)"  :class="selected_import?'cj-button selected':'cj-button'">
                    稍后备份
                </mt-button>
            </div>
            <div v-if="$route.query.type=='2'" style="padding-top:4rem;">
                <p style="padding:0 2.6rem">钱包导入成功，请妥善保管好你的密码</p>
                <mt-button @click.native="newpurse_S(3)" class="cj-button selected">
                    确定
                </mt-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                selected_newpurse:true,
                selected_import:false
            }
        },
        methods:{
            newpurse_S(val){
                if(val == 1){
                    this.selected_newpurse = true;
                    this.selected_import=false;
                    this.$router.push('/backups')
                }
                if(val == 2){
                    this.selected_newpurse = false;
                    this.selected_import=true;
                    this.$router.push('/')
                }
                if(val == 3){
                    this.$router.push('/')
                }
            }
        }
    }
</script>

<style lang="less">
    .cresuccess{
        .text-success{
            padding-top: 6.2rem;
            font-family:Lato-Regular;
            font-size:3.6rem;
            color:#ffffff;
            letter-spacing:0;
            text-align:center;
            line-height:4.8rem;
        }
        .success-img{
            width: 100%;
            height:26rem;
            margin-top: 7.3rem;
            margin-bottom:9.2rem;
            .img-icon{
                // width:26rem;
                text-align: center;
                position: relative;
                img{
                    width:26rem;
                    position: absolute;
                    left:50%;
                    top:0;
                    margin-left: -13rem;
                }
                .img2{
                    transform: scale(0.7);
                }
                .img3{
                    transform: scale(0.4);
                }
            }
        }
        .cresuccess-button{
            text-align: center;
            width:100%;
            margin-top: 9.8rem;
            padding:0 3rem;
            margin-bottom: 5rem;
            p{
                font-family:PingFangSC-Semibold;
                letter-spacing:0.1rem;
                font-size:1.2rem;
                color:#66688f;
                text-align:center;
                line-height:1.6rem;
                margin-bottom: 1.7rem;
                padding: 0 5.6rem;
            }
            .cj-button{
                border:none;
                background: none;
                box-shadow: none;
                width: 100%;
            }
            .cj-button:hover{
                background-image:linear-gradient(120deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:10rem;
                height:5rem;
                border:none;
                margin-bottom: 1rem;
                color: #fff;
            }
            .selected{
                // background-image:linear-gradient(-131deg, #fc5d9f 0%, #5863fc 100%);
                background-image:linear-gradient(120deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:10rem;
                height:5rem;
                border:none;
                margin-bottom: 1rem;
                color: #fff;
            }
        }
    }
</style>



