<template>
    <div class="addassets-search main-content">
        <mt-header class="created-header" fixed title="搜索">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
            <!-- <mt-button slot="right"><i class="fa fa-sousuo"></i></mt-button> -->
        </mt-header>
        <div class="addassets-list">
            <div class="search">
                <i class="fa fa-sousuo"></i>
                <input type="text" class="input-swach">
            </div>
            <div class="addassets-l-info clear">
                <div class="curreny-icon float-left">
                    <img src="/static/img/btc-ico@1x.png" alt="">
                </div>
                <div class="curreny-name-num float-left">
                    <p class="curreny-name">BTC</p>
                    <p class="curreny-money">≈0.06CNY</p>
                </div>
                <div class="checkes-add float-right">
                    <mt-switch class="addasset-checked" v-model="switchvalue" @change="addassets"></mt-switch>
                </div>
            </div>
            <div class="addassets-l-info clear">
                <div class="curreny-icon float-left">
                    <img src="/static/img/btc-ico@1x.png" alt="">
                </div>
                <div class="curreny-name-num float-left">
                    <p class="curreny-name">BTC</p>
                    <p class="curreny-money">≈0.06CNY</p>
                </div>
                <div class="checkes-add float-right">
                    <mt-switch class="addasset-checked" v-model="switchvalue1" @change="addassets"></mt-switch>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                switchvalue:false,
                switchvalue1:false
            }
        },
        methods:{
            addassets(val){
                console.log(val)
            }
        }
    }
</script>

<style lang="less">
    .addassets-search{
        padding:40px 2rem 0;
        color:#fff;
        .created-header{
            background: none;
        }
        .addassets-list{
            width:100%;
            height:100%;
            .search{
                padding: 0 1rem;
                position: relative;
                .input-swach{
                    background: none;
                    width:100%;
                    border:none;
                    border-bottom: 1px solid #2d2e3b;
                    padding: 1.5rem 2rem 1.5rem 4rem;
                    font-size:1.6rem;
                    font-weight: 500;
                    color: #fff;
                }
                i{
                    position:absolute;
                    left:2rem;
                    top:50%;
                    transform: translate3d(0,-50%, 0);
                }
            }
            .addassets-l-info{
                padding:2.2rem 0 ;
                border-bottom:1px solid #2d2e3b;
                .curreny-icon{
                    width:3.8rem;
                    height:3.8rem;
                    img{
                        width:100%;
                    }
                }
                .curreny-name-num{
                    height:3.8rem;
                    margin-left: 2.4rem;
                    position:relative;
                    .curreny-name{
                        position: absolute;
                        top:0;
                        left:0;
                        font-size:1.4rem;
                        font-weight:600;
                    }
                    .curreny-money{
                        position: absolute;
                        bottom:0;
                        left:0;
                        color:#b5b5b5;
                    }
                }
                .checkes-add{
                    padding-top:.2rem;
                    .addasset-checked{
                        .mint-switch-core {
                            height: 3.4rem;
                            border: 1px solid #3d3d4c ;
                            background: #3d3d4c;
                        }
                        .mint-switch-input:checked + .mint-switch-core {
                            background-image:linear-gradient(-131deg, #fc5d9f 0%, #5863fc 100%) ; 
                        }
                       
                        .mint-switch-core::before{
                            background-color: #3d3d4c;
                        }
                        .mint-switch-core::after{
                            top:1px;
                        }
                    }
                    
                }
                
                
            }
        }
    }
</style>



