<template>
    <div class="leading-in-wallet main-content">
        <mt-header class="created-header" fixed title="导入钱包">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
        </mt-header>
        <div class="leading-content">
            <mt-navbar v-model="selected">
                <mt-tab-item id="1">助记词</mt-tab-item>
                <mt-tab-item id="2" style="">KEYSTORE</mt-tab-item>
                <mt-tab-item id="3">私钥</mt-tab-item>
            </mt-navbar>

            <!-- tab-container -->
            <mt-tab-container v-model="selected">
                <mt-tab-container-item id="1" class="container-text">
                    <div class="private-key">
                        <textarea name="" id="" placeholder="请输入助记词，按空格分开"></textarea>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="2">
                    <div class="private-key">
                        <p class="p-info">直接复制粘贴以太坊官方钱包Keystore文件内容至输入框</p>
                        <textarea name="" id="" placeholder="Keystore文本内容"></textarea>
                    </div>
                     <div class="created-form-name">
                        <p>Keystore密码</p>
                        <input type="password" v-model="keystorepassword" placeholder="请输⼊Keystore密码" style="letter-spacing:5px;">
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="3">
                    <div class="private-key">
                        <p class="p-info">私钥是区块链系统中最为重要的信息，用户转账时的签名。在输入框里粘贴明文私钥</p>
                        <textarea name="" id="" placeholder="请输入私钥"></textarea>
                    </div>
                </mt-tab-container-item>
            </mt-tab-container>
            <div class="leading-in-footer">
                <mt-button @click.native="daoruwallet" class="cj-button selected">
                        下一步
                </mt-button>
            </div>
        </div>
        
    </div>
</template>

<script>
    export default {
        data() {
            return {
                selected:"1",
                keystorepassword:''
            }
        },
        methods:{
            daoruwallet(){
                this.$router.push({
                    path:'/created',
                    query:{
                        type:2
                    }}
                )
            }
        }
    }
</script>

<style lang="less">
.leading-in-wallet{
    padding-top:4rem;
    width: 100%;
    height: 100%;
    .created-header{
        background: none;
    }
    .leading-content{
        width: 100%;
        height: 100%;
        padding:2rem 2rem;
        min-height: 50rem;
        position: relative;
        .mint-navbar{
            background: none;
            color: #66688f;
            padding: 0 3rem;
            font-size:1.2rem;
            .mint-tab-item.is-selected{
                color: #fff;
                border-bottom: none;
            }
            .mint-tab-item.is-selected:after {
                content: "";
                width: 10px;
                border-radius: 100px;
                height: 2px;
                display: inline-block;
                background: #fc5d9f;
            }
        }
        .private-key{
            background:#2e2f47;
            box-shadow:0 0 3.3rem 0 rgba(34,34,44,0.25);
            border-radius:1.3rem;
            width:100%;
            padding:3.6rem 3rem;
            vertical-align: middle;
            textarea{
                background:#ffffff;
                border-radius:1.3rem;
                width:100%;
                height:13.9rem;
                padding:1.5rem 2rem;
                font-size: 1.4rem;
                color: #9fa7b3;
                border:none;
                resize:none
            }
            .p-info{
                // font-family:PingFangSC-Regular;
                font-size:1.4rem;
                color:#9fa7b3;
                letter-spacing:0.19px;
                text-align:left;
                margin-bottom: 1.5rem;
            }
        }
        
        .leading-in-footer{
            width: calc(100% - 4rem);
            position:absolute;
            bottom:5rem;
            .cj-button{
                border:none;
                background: none;
                box-shadow: none;
                width: 100%;
            }
            .cj-button:hover{
                background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
            .selected{
                background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
        }
        @media screen and (min-width:414px){
            .leading-in-footer{
                width: 100%;
                position:static;
                margin-top:10rem;
                margin-bottom:5rem;
            }
        }
        @media screen and (max-width:320px){
            .leading-in-footer{
                width: 100%;
                margin-top: 20rem;
                position:static;
                bottom:0rem;
                margin-bottom: 5rem;
                // position:absolute;
                // bottom:3rem;
                .cj-button{
                    border:none;
                    background: none;
                    box-shadow: none;
                    width: 100%;
                }
                .cj-button:hover{
                    background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                    border-radius:100px;
                    height:50px;
                    border:none;
                    margin-bottom: 10px;
                    color: #fff;
                }
                .selected{
                    background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                    border-radius:100px;
                    height:50px;
                    border:none;
                    margin-bottom: 10px;
                    color: #fff;
                }
            }
        }
        .created-form-name{
                padding: 2rem 1rem;
            input::-webkit-input-placeholder { /* WebKit browsers */ 
                font-size:1.2rem;
                letter-spacing:1px;
                color:#66688f;
            } 
            input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
                font-size:1.2rem;
                letter-spacing:1px;
                color:#66688f;
            } 

            input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
                font-size:1.2rem;
                letter-spacing:1px;
                color:#66688f;
            } 
            input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
                font-size:1.2rem;
                letter-spacing:1px;
                color:#66688f;
            }
            p{
                font-size:12px;
                color: #66688f;
            }
            input{
                width:100%;
                border:none;
                border-bottom:1px solid #2e2f47;
                background: none;
                padding:22px 0;
                font-size: 16px;
                color:#fff;
            }
        }
        
    }
    
}
    
</style>



