<template>
    <div class="backups main-content">
        <mt-header class="created-header" fixed title="备份助记词">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
        </mt-header>
        <div class="backups-content" style="height:100%;width:100%;">
            <div class="private-key">
                <p class='zjc-info' style="margin-bottom:3rem;">验证助记词</p>
                <p class='zjc-info' style="font-size:1.2rem;">按顺序点击助记词，确认你的助记词备份正确</p>
                <textarea name="" readonly id="" placeholder="" v-model="zhujici"></textarea>
                <div class="zjc-list">
                    <button v-for="(item,index) in zhujicilist" :key="index" @click="selectzjc(item)" :class="{'spanlist':(index+1)%3 != 0,'spanlist-last':(index+1)%3 == 0,active:item.show === true}" :disabled="item.show">{{item.text}}</button>
                </div>
            </div>
            <mt-button @click.native="sure_backups" class="tonext">
                确定
            </mt-button>
        </div>
        <Alert :ModelOptions="confirmOptions" ref="alertModel"/>
    </div>
</template>
<script>
import { MessageBox } from 'mint-ui';
import Alert from '../com/alert.vue';
    export default {
        components:{
            Alert
        },
        data() {
            return {
                zhujici:'',
                // zhujicilist:['asdsa','1243243','qwrew',"qsdfqa","qwre","dsfgs",'asdsa','1243243','qwrew',"qsdfqa","qwre","dsfgs"],
                zhujicilist:[
                    {
                        text:"asfq",
                        show:false
                    },
                    {
                        text:"as31dq",
                        show:false
                    },
                    {
                        text:"64576546",
                        show:false
                    },
                    {
                        text:"asasdfs",
                        show:false
                    },
                    {
                        text:"asdfq",
                        show:false
                    },
                    {
                        text:"asasfdfq",
                        show:false
                    },
                    {
                        text:"asdfa",
                        show:false
                    },
                    {
                        text:"asdsad",
                        show:false
                    },
                    {
                        text:"asd",
                        show:false
                    },
                    {
                        text:"asdfq",
                        show:false
                    },
                    {
                        text:"asdfq",
                        show:false
                    },
                    {
                        text:"asdfq",
                        show:false
                    }
                ],
                confirmOptions:{}
            }
        },
        methods:{
            selectzjc(val){
                val.show = !val.show
                this.zhujici = this.zhujici + ' '+ val.text
            },
            sure_backups(){
                let that = this;
                this.$refs.alertModel.showModel();
                this.confirmOptions= {
                    img:true,//弹框显示图片 需要传imgsrc
                    imgsrc:'/static/img/shibai.png',//图片路径
                    type: "1",// 1:普通提示框 2:textarea提示框，需要传入 inputvalue,readonly
                    inputvalue:"qwr124f1wt24tef34t235g23yj4fqwf567f23fgtrbqewftyn43btn",//textarea的值
                    readonly:false,// 是否只读
                    title: "备份失败",//标题
                    message: "请检查你的助记词",//提示信息
                    btnText: "取消",//按钮文字
                    notice:"",//特殊警告信息   如：警告：私钥未加密，泄漏风险极大，请保存在安全位置！
                    btnCancelFunction: function (val) {//回调函数
                        console.log(that.confirmOptions.inputvalue)
                        // alert(val)
                    }
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .backups{
        .created-header{
            background: none;
        }
        .backups-content{
            padding:8rem 2rem 0 2rem;

        }
        .private-key{
            background:#2e2f47;
            box-shadow:0 0 3.3rem 0 rgba(34,34,44,0.25);
            border-radius:1.3rem;
            width:33.5rem;
            padding:3.6rem 3rem;
            vertical-align: middle;
            textarea{
                background:#ffffff;
                border-radius:1.3rem;
                width:100%;
                height:8.9rem;
                padding:1.5rem 2rem;
                font-size: 1.4rem;
                border:none;
                resize:none;
                color:#2e2f47;
                letter-spacing:0.19px;
            }
            .zjc-info{
                // font-family:PingFangSC-Regular;
                text-align: center;
                font-size:1.4rem;
                color:#9fa7b3;
                letter-spacing:0.19px;
                margin-bottom: 1.5rem;
            }
            .zjc-list{
                margin-top:2rem;
                width:100%;
                button{
                    display: inline-block;
                    width:30%;
                    font-size:1.4rem;
                    padding:.2rem 0;
                    color:#ffffff;
                    letter-spacing:0.19px;
                    text-align:center;
                    background:#222337;
                    border-radius:1.3rem;
                    margin-bottom: 1rem;
                    border:none;
                    // margin: 0 1.8% 1rem 1.8%;
                }
                .spanlist{
                    margin-right: 5%;
                }
                .active{
                    opacity: 0.5;
                }
            }
        }
        .tonext{
            position:absolute;
            bottom:5rem;
            background-image:linear-gradient(120deg, #fc5d9f 0%, #5863fc 100%);
            border-radius:100px;
            width:calc(100% - 4rem);
            height:50px;
            color:#fff;
            border:none;
        }
        @media screen and (max-width:320px) {
            .tonext{
                position: static;
                margin: 5rem 0;
                background-image:linear-gradient(120deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                width:calc(100%);
                height:50px;
                color:#fff;
                border:none;
            }
        }
    }
</style>



