<template>
    <div class="backups main-content">
        <mt-header class="created-header" fixed title="备份助记词">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
        </mt-header>
        <div class="backups-content" style="height:100%;width:100%;">
            <div class="private-key">
                <p class='zjc-info'>记下助记词</p>
                <p class='zjc-info'>请确认你所在的环境是安全隐私的，任何人拿到你的助记词都可以转走你的数字资产！</p>
                <textarea name="" readonly id="" placeholder="" v-model="zhujici"></textarea>
            </div>
            <div class="tbtx">
                <p>特别提醒：</p>
                <p>助记词非常重要！不要丢失或泄漏，如果有人获取您的助记词将直接获取您的资产！</p>
            </div>
            <mt-button @click.native="toverification" class="tonext">
                下一步
            </mt-button>
        </div>
        
    </div>
</template>

<style lang="less" scoped>
    .backups{
        .created-header{
            background: none;
        }
        .backups-content{
            padding:8rem 2rem 0 2rem;

        }
        .private-key{
            background:#2e2f47;
            box-shadow:0 0 3.3rem 0 rgba(34,34,44,0.25);
            border-radius:1.3rem;
            width:33.5rem;
            padding:3.6rem 3rem;
            vertical-align: middle;
            textarea{
                background:#ffffff;
                border-radius:1.3rem;
                width:100%;
                height:8.9rem;
                padding:1.5rem 2rem;
                font-size: 1.4rem;
                border:none;
                resize:none;
                color:#2e2f47;
                letter-spacing:0.19px;
            }
            .zjc-info{
                // font-family:PingFangSC-Regular;
                text-align: center;
                font-size:1.4rem;
                color:#9fa7b3;
                letter-spacing:0.19px;
                margin-bottom: 1.5rem;
            }
        }
        .tbtx{
            margin-top: 4.6rem;
            p{
                margin-bottom: 1.5rem;
                font-size:14px;
                color:#fc5d9f;
                letter-spacing:0;
                line-height:16px;
                text-align:left;
            }
        }
        .tonext{
            position:absolute;
            bottom:5rem;
            background-image:linear-gradient(-131deg, #fc5d9f 0%, #5863fc 100%);
            border-radius:100px;
            width:calc(100% - 4rem);
            height:50px;
            color:#fff;
            border:none;
        }
        @media screen and (max-width:320px) {
            .tonext{
                position: static;
                margin: 5rem 0;
                background-image:linear-gradient(-131deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                width:calc(100%);
                height:50px;
                color:#fff;
                border:none;
            }
        }
    }
</style>

<script>
    export default {
        data() {
            return {
                zhujici:'dsfafd sadfsaf safds fdsf dsf sf safd f safdsafds '
            }
        },
        methods:{
            toverification(){
                this.$router.push('/verification');
            }
        }
    }
</script>


