<template>
    <div class="account main-content">
        <mt-header class="created-header" fixed title="转账">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
            <mt-button slot="right"><i class="fa fa-saomiao"></i></mt-button>
        </mt-header>
        <div class="account-form">
            <div class="account-form-name">
                <p>收款方地址</p>
                <input type="text"  placeholder="请输入收款地址">
            </div>
            <div class="account-form-name">
                <p>币种</p>
                <input type="text" placeholder="币种">
            </div>
            <div class="account-form-name">
                <p>数量</p>
                <input type="text" placeholder="数量">
            </div>
            <div class="account-form-name">
                <p>备注</p>
                <input type="text" placeholder="请输入">
            </div>
            <div class="account-form-name">
                <p>矿工费</p>
                <mt-range
                class="account-range"
                v-model="rangeValue"
                :min="10"
                :max="100"
                :barHeight="6">
                </mt-range>
                <p style="text-align:center;font-size:14px;"><span class="float-left">慢</span><span style="color:#fff;">{{ rangeValue }} ether</span><span class="float-right">快</span></p>
            </div>
            
            <div class="account-button">
                <mt-button  class="cj-button selected">
                    转账
                </mt-button>
                <!-- <mt-button @click.native="newpurse(2)"  :class="selected_import?'cj-button selected':'cj-button'">
                    导入钱包
                </mt-button> -->
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                rangeValue:20
            }   
        }
    }
</script>

<style lang='less'>
    .account{
        padding-top:40px;
        .account-header{
            background: none;
            .fa-saomiao{
                font-size:20px;
            }
        }
        .account-form{
            width:100%;
            padding: 3.4rem 3rem 3rem;
            min-height:100%;
            position: relative;

            .account-form-name{
                margin-bottom: 3rem;
                input::-webkit-input-placeholder { /* WebKit browsers */ 
                    font-size:16px;
                    letter-spacing:1px;
                    color:#fff;
                } 
                input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
                    font-size:16px;
                    letter-spacing:1px;
                    color:#fff;
                } 

                input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
                    font-size:16px;
                    letter-spacing:1px;
                    color:#fff;
                } 
                input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
                    font-size:16px;
                    letter-spacing:1px;
                    color:#fff;
                } 
                .text_error{
                    height:35px;
                    line-height: 30px;
                    color:#f00;
                    font-size:12px;
                }
                p{
                    font-size:12px;
                    color: #66688f;
                }
                input{
                    width:100%;
                    border:none;
                    border-bottom:1px solid #2e2f47;
                    background: none;
                    padding:22px 0;
                    font-size: 16px;
                    line-height: 2rem;
                    color:#fff;
                }
                .account-range{
                    margin-top:2rem;
                    .mt-range-runway{
                        border-radius: 10px;
                    }
                    .mt-range-progress{
                        border-radius: 10px 0 0 10px;
                        background-color:#bf5fc1;
                    }
                    .mt-range-thumb{
                        // width:21px;
                        // height:21px;
                        // top:4px;
                    }
                    
                }
            }
            .account-button{
                // position: absolute;
                text-align: center;
                width:100%;
                margin-top: 10rem;;
                // bottom:80px;
                p{
                    font-size:12px;
                    color:#66688f;
                    letter-spacing:0.6px;
                    text-align:center;
                    line-height:14px;
                    margin-bottom: 17px;
                }
                .cj-button{
                    border:none;
                    background: none;
                    box-shadow: none;
                    width: 100%;
                }
                .cj-button:hover{
                    background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                    border-radius:100px;
                    height:50px;
                    border:none;
                    margin-bottom: 10px;
                    color: #fff;
                }
                .selected{
                    background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
                    border-radius:100px;
                    height:50px;
                    border:none;
                    margin-bottom: 10px;
                    color: #fff;
                }
            }
        }
    }
</style>


